*,
*::after,
*::before{

    box-sizing: border-box;

}

html, body{

  display: grid;

  height: 100vh;

}

#root{

  height: 100%;

}

.root{

  position: relative;

  width: 100%;

  height: 100%;

  --title: Vollkorn;

  --normal: Varela;

  font-family: Varela;

}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

input{

  outline: none;

}

p{

  margin: 0;

}
header{

    width: 100%;

    height: 80px;
    
    padding: 10px;

    padding-left: 20px;
    padding-right: 50px;

    display: flex;

    justify-content: space-between;

    align-items: center;

    font-size: 1.5em;

    font-weight: bold;

    font-family: var(--title);

    position: -webkit-sticky;

    position: sticky;
    top: 0;

    z-index: 500;

    color: rgb(0, 0, 0);

    background-color: #0077ff;

}

header .logo{

    background-image: url(/stellar-icon/static/media/logo.1c2e68a8.svg);

    background-size: 100% 100%;

    width: 70px;

    height: 70px;

    background-position: center;

    border-radius: 4px;

}

header .car-parent{

    z-index: -100;

}

header .car{

    height: 100%;

    width: 45px;

    height: 45px;

    background-color: white;

    box-sizing: content-box;

    padding: 5px;

    border-radius: 10px;

    color: black;

    z-index: -100;

}

header .modal{

    background-color: rgba(0, 0, 0, 0.5);

    -webkit-backdrop-filter: blur(10px);

            backdrop-filter: blur(10px);

    position: absolute;

    width: 100%;

    height: 100vh;

    top: 0;

    left: 0;

    z-index: 500;

    display: flex;

    justify-content: center;

    align-items: center;

    color: black;

}

header .modal .close{

    position: absolute;

    width: 100%;

    height: 100%;

}

header .modal .close .cross{

    color: white;

    width: 70px;

    height: 70px;

    position: absolute;

    right: 20px;

    top: 20px;

    background-color: red;

    border-radius: 4px;

    box-shadow: 0 1px 2px black;

    cursor: pointer;

}

header .modal .products{

    width: 80%;

    height: 80%;

    background-color: white;

    z-index: 200;

    border-radius: 10px;

    display: grid;

    grid-template-rows: auto -webkit-min-content 100px;

    grid-template-rows: auto min-content 100px;

    padding: 20px;

    grid-gap: 15px;

    gap: 15px;

    box-shadow: 0 0 5px black;

}

header .modal .products > button{

    background-color: gold;

    font-size: 2rem;

    box-shadow: 0 2px 2px black;

}

header .modal .products .total{

    font-weight: bold;

    font-family: Varela;

}

header .modal .products .total span{

    color: green;

}

header .modal .products .item-list{

    width: 100%;
    height: 100%;

    overflow-y: scroll;

    display: grid;

    grid-template-columns: repeat(4, 1fr);

    justify-items: center;

    position: relative;

    grid-gap: 10px;

    gap: 10px;

}

header .modal .products .item-list h2{

    position: absolute;

    font-size: 1.5em;

    color: grey;

    font-family: sans-serif;

    top: 40%;

} 

header .modal .products .item-list .item{

    display: flex;

    justify-content: center;

    align-items: center;

    flex-direction: column;

    height: -webkit-min-content;

    height: -moz-min-content;

    height: min-content;

    padding: 15px;

    width: 100%;

    border-radius: 3px;

}

header .modal .products .item-list .item > *:not(:last-child){

    margin-bottom: 6px;

}

header .modal .products .item-list .item .price{

    font-family: var(--normal);

    color: green;

}

header .modal .products .item-list .item button{

    background-color: red;

    color: white;

    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.5);

    width: 100%;

    font-size: 1.1rem;

}

header .modal .products .item-list .item .img{

    height: 100px;

    width: 100px;

    background-size: 100% 100%;

}

header .car-container{

    padding: 0;

}
header .pay{

    position: absolute;

    top: 0;

    left: 0;

    width: 100%;

    height: 100vh;

    background-image: linear-gradient(45deg, rgb(3, 0, 194), rgb(204, 0, 255));

    font-family: var(--normal);

    display: flex;

    justify-content: center;

    align-items: center;

}

header .pay .close{

    background-color: red;

    color: white;

    position: absolute;

    top: 20px;

    right: 20px;

    width: 80px;

    height: 80px;

    border-radius: 4px;
    box-shadow: 0 1px 2px black;

    cursor: pointer;

}

header .pay .progress{

    position: absolute;

}

header .pay > *{

    width: 100%;

    height: 100%;

}

header .pay .data{

    display: flex;

    flex-direction: column;

    justify-content: center;

    align-items: center;

}

header .pay .data h2{

    margin: 0;

    margin-bottom: 30px;

    background-color: rgb(3, 0, 194);

    padding: 10px;

    padding-left: 40px;
    padding-right: 40px;

    border-radius: 4px;

    box-shadow: 0 0 4px -2px black;

    text-shadow: 0 0 3px black;

    color: white;

}

header .pay .data .container{

    display: grid;

    flex-direction: column;

    justify-content: center;

    align-items: flex-start;

    grid-template-rows: repeat(5, 1fr);

    font-size: 2rem;

    background-color: white;

    padding: 50px;

    height: 600px;

    border-radius: 10px;

    grid-gap: 10px;

    gap: 10px;

    box-shadow: 0 0 10px -2px black;

}

header .pay .data .container .container-2{

    width: 100%;

    height: 100%;

    display: grid;

    grid-template-columns: repeat(2, 1fr);

    grid-gap: 50px;

    gap: 50px;

}

header .pay .data .input{

    width: 100%;

}

header .pay .data .logo-card{

    display: flex;

}

header .pay .data .visa{

    width: 200px;

    height: 100px;

}

header .pay .data button{

    background-color: greenyellow;

    padding: 1em;

    padding-left: 2em;

    padding-right: 2em;

    font-size: 1.5rem;

    box-shadow: 0 2px 6px -2px black;

}

.check{

    position: absolute;

    top: 0;

    left: 0;

    width: 100%;

    height: 100vh;

    background-color: black;

    background-image: linear-gradient(45deg, rgb(204, 0, 255), rgb(0, 255, 0));

    display: flex;

    justify-content: center;

    align-items: center;

    flex-direction: column;

    font-family: var(--normal);
    

}

.check .close{

    background-color: red;

    position: absolute;

    top: 20px;

    right: 20px;

    width: 80px;

    height: 80px;

    border-radius: 4px;

    box-shadow: 0  1px 2px black;

    cursor: pointer;

    color: white;

}

.modal-check{

    justify-content: center;

    align-items: center;

    position: absolute;

    width: 100%;

    height: 100%;

    background-color: rgba(0, 0, 0, 0.8);

    z-index: 500;

    font-size: 1.3em;

    color: rgb(175, 175, 175);

    -webkit-backdrop-filter: blur(10px);

            backdrop-filter: blur(10px);

}

.check .progress{

    position: absolute;

    z-index: 500;

}

.check h2{

    margin: 0;

    margin-bottom: 30px;

    padding: 20px;

    border-radius: 5px;

    background-color: rgb(204, 0, 255);

    box-shadow: 0 0 5px -2px black;

    color: white;

}

.check .container{

    height: 700px;

    width: 900px;

    background-color: white;

    border-radius: 10px;

    display: grid;

    grid-template-rows: 0.8fr -webkit-min-content 0.2fr;

    grid-template-rows: 0.8fr min-content 0.2fr;

    box-shadow: 0 0 10px -2px black;

    padding: 20px;

    grid-gap: 20px;

    gap: 20px;

}

.check .container .total{

    color: black;

}

.check .container .total span{

    color: green;

}

.check .container button{

    background-color: greenyellow;

    font-size: 2rem;

    box-shadow: 0 1px 3px black;

}

.check .container .products{

    display: grid;

    grid-template-columns: repeat(3, 1fr);

    grid-auto-rows: -webkit-min-content;

    grid-auto-rows: min-content;

    grid-gap: 20px;

    gap: 20px;

    overflow-y: scroll;

}

.check .container .products .item{

    width: 100%;

    display: grid;

    flex-direction: column;

    justify-items: center;

    align-items: center;

    padding: 15px;

    border-radius: 3px;

    grid-gap: 10px;

    gap: 10px;

}

.check .container .products .item .price{

    color: green;

}

.check .container .products .item .img{

    height: 100px;

    width: 100px;

    background-size: 100% 100%;

}

.check .container .products .item button{

    background-color: red;

    width: 100%;

    color: white;

    font-size: 1.5rem;

    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.5);

}
.main{

    height: 100%;

    display: flex;

}

.main .search{

    width: 200px;

    height: auto;

    display: flex;

    flex-direction: column;

    padding: 10px;

    padding-top: 40px;

    border-right: 1px rgb(202, 202, 202) solid;

}

.main .search h5{

    margin-top: 15px;

}

.main .search .categories{

    display: flex;

    flex-direction: column;

    width: 100%;

}

.main .search .categories .container{

    margin: 0;

}

.main .search .input-search {

    margin-bottom: 18px;

}

.main .search .container{

    display: flex;

    justify-content: space-between;

    align-items: center;

    margin-bottom: 18px;

}

.main .shop{

    width: 100%;

    height: 100%;

    display: grid;

    grid-template-columns: repeat(4, 1fr);

    grid-auto-rows: -webkit-min-content;

    grid-auto-rows: min-content;

    justify-items: center;

    padding: 20px;

}

.main .shop .item{

    width: 100%;

    height: -webkit-min-content;

    height: -moz-min-content;

    height: min-content;

    padding: 5px;

    padding-top: 20px;
    padding-bottom: 20px;

    display: grid;

    grid-template-rows: auto  -webkit-min-content -webkit-min-content 35px;

    grid-template-rows: auto  min-content min-content 35px;

    grid-gap: 12px;

    gap: 12px;

    border-radius: 4px;

    justify-items: center;

    position: relative;

    position: relative;

    bottom: 0;

    transition: all 200ms;

    color: rgb(180, 180, 180);

}

.main .shop .item:hover button{

    opacity: 1;

    transition: opacity 200ms;

}
.main .shop .item:hover{

    bottom: 5px;

    transition: all 200ms;

    box-shadow: 0 0 3px rgba(0, 0, 0, 0.6);

    color: rgb(78, 78, 78);

}

.main .shop .item .description{

    margin-right: auto;

}

.main .shop .item .img{

    background-size: 100% 100%;

    background-position: center;

    height: 150px;

    width: 150px;

    /* border-bottom: 1px rgb(129, 129, 129) solid; */

}

.main .shop .item .name{

    font-weight: bold;

}

.main .shop .item button{

    border: none;

    border-radius: 4px;

    font-weight: bold;
    
    width: 100%;
    
    color: inherit;

    background-color: rgb(239, 255, 239);

    opacity: 0;

    transition: opacity 200ms;

}

.main .shop .item .container{

    display: flex;

    width: 100%;

    justify-content: space-evenly;

}

.main .shop .item .categories{

    width: 100%;

    height: 100%;

    display: grid;

    grid-template-columns: repeat(2, 1fr);

    justify-items: center;

}

.main .shop .item .categories > *{

    margin-right: 5px;

    font-style: italic;

    position: relative;

}

.main .shop .item .price{

    color: rgb(100, 211, 100);

    font-weight: bold;

}

footer{

    height: 50px;

    background-color: rgb(172, 172, 172);

    display: flex;
    
    justify-content: center;

    align-items: center;

    font-weight: bold;

    font-size: 1rem;

    border-top: 1px black solid;

    font-family: var(--title);

}
